  /* HS집토끼체 */
  @font-face {
    font-family: 'HSJiptokki-Black';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/HSJiptokki-Black.woff2') format('woff2');
    font-weight: 900;
    font-style: normal;
  }
  
  /* 라인시드체 */
  @font-face {
    font-family: 'LINESeedKR-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Bd.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
  }
  
  /* 디자인하우스체 */
  @font-face {
    font-family: 'designhouseOTFLight00';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_three@1.0/designhouseOTFLight00.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

  /* 리디바탕체 */
  @font-face {
    font-family: 'RIDIBatang';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_twelve@1.0/RIDIBatang.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

  .Common-all {
    text-align: center;
    word-break: keep-all;
  }

  .Common-header {
    background-color: #282c34;
/*    min-height: 100vh;*/
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* font-size: calc(10px + 2vmin); */
    font-size: 10rem;
/*    color: white;*/
  }

  .Common-sub-header {
    /* position: absolute; */
    background-color: #282c34;
/*    min-height: 100vh;*/
/*    height:100px; */
    height: auto;


    /* display: flex; */
    /* flex-direction: row; */
    /* margin-left: auto;
    margin-right: auto; */
    align-items: center;
    justify-content: center;
    /* font-size: calc(10px + 2vmin); */
    font-size: 10rem;
/*    color: white;*/
    color: #282c34;
/*    border-bottom: white solid 1px;*/
  }

  .Common-body {
    background-color: #282c34;
/*    min-height: 100vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .Common-footer {
    /* border-top: 1px solid #261A1A;
    margin-left: 10px; */
    /* background-color: white; */
    background-color: #FFF7F2;
    height: 16vmin;
/*    min-height: 100vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    /* color: black; */
    color: #261A1A;
  }

  .Common-top-title {
    font-family: 'HSJiptokki-Black';
    font-size: 7vmin;
    /* margin-top: 10px; */
    color: white;
    background-color: #282c34;
    padding-top: 2vh;
    padding-bottom: 2vh;
  }

  .Common-sub-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    margin-left: 15px;
    margin-right: 15px;
    /* margin-bottom: 30px; */
  }
  
  .Common-sub-title {
    font-family: 'RIDIBatang';
    /* font-size: calc(10px + 2vmin); */
    font-size: 2.5rem;
    width: fit-content;
    text-align: center; 
    margin: 0 auto;
    margin-bottom: 20px;
    background: linear-gradient(to top, #c7c1b9 30%, transparent 30%);

    /* background: linear-gradient(to top, #353841 35%, transparent 35%); */
    /* background: linear-gradient(to top, #FFE400 35%, transparent 35%); */
  }
  
  .Common-sub-content {
    font-family: 'RIDIBatang';
    /* font-size: calc(5px + 2vmin); */
    font-size: 2rem;
    white-space: pre-wrap;
    margin-bottom: 20px;
  }

  .Common-sub-description {
    font-family: 'RIDIBatang';
    /* font-size: calc(2vmin); */
    font-size: 2rem;
    white-space: pre-wrap;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 20px;
  }

  .Common-empty-area-50 {
    height: 50px;
  }
  .Common-empty-area-20 {
    height: 20px;
  }

  .Common-empty-area-width-20 {
    width: 20px;
  }

  .Common-empty-area-width-80pecent {
    width: 80%;
  }

  .Common-content-block {
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
  }
  
  .Common-content-img {
    width: 33%;
    margin-bottom: 20px;
  }

  .Common-link {
    color: #61dafb;
    font-family: 'RIDIBatang';
    font-size: calc(5px + 2vmin);
  }

  .Common-back-btn {
    position: absolute;
    height : 6vmin;
    width : 6vmin;
    top: 1vh;
    left: 1vh;
    filter: invert(100%);
    background-image : url('../img/arrow.png');  
    background-size : cover;
    color: white;
    /* background-position : right;
    align-items: center;
    justify-content: center; */
  }

  .Common-home-btn {
    position: absolute;
    height : 5vmin;
    width : 5vmin;
    top: 3vmin;
    left: 3vmin;
    filter: invert(100%);
    background-image : url('../img/home.png');  
    background-size : cover;
    color: white;
    /* background-position : right;
    align-items: center;
    justify-content: center; */
  }

  .Common-fixed-top-btn {
    position: fixed;
    height : 5vmin;
    width : 5vmin;
    bottom: 0;
    right: 0;
    margin-right: 20px;
    margin-bottom: 20px;
    filter: invert(100%);
    opacity: 0.5;

    background-image : url('../img/arrow.png');  
    transform: rotate(90deg);
    color: white;
    background-size : cover;
    background-position : right;
    align-items: right;
    justify-content:right;
  }

  .Common-absolute-top-btn {
    position: absolute;
    height : 5vmin;
    width : 5vmin;
    bottom: 0;
    right: 0;
    margin-right: 20px;
    margin-bottom: 20px;
    filter: invert(100%);
    opacity: 0.5;

    background-image : url('../img/arrow.png');  
    transform: rotate(90deg);
    color: white;
    background-size : cover;
    background-position : right;
    align-items: right;
    justify-content:right;
  }


  .Common-position-relative {
    position: relative;
  } 

  .Common-menu-btn {
    position: fixed;
    height : 6vmin;
    width : 6vmin;
    top: 3vmin;
    right: 3vmin;
    filter: invert(100%);
    background-image : url('../img/menu.png');  
    color: white;
    background-size : cover;
    /* background-position : right;
    align-items: center;
    justify-content: center; */
  }

  .Common-close-btn {
    height : 6vmin;
    width : 6vmin;
    filter: invert(100%);
    background-image : url('../img/close.png');  
    background-size : cover;
    text-align: right;
    align-items: right;
    justify-content: right;
    display: flex;
    flex-direction: row;

    margin-top: 2vmin;;
    margin-left: 2vmin;
    margin-bottom: 2vmin;
  }

  .Common-show-menu{
    display: flex;
    flex-direction: column;
    width: 90vh;
    max-width: 300px;
    height: 100vh;
    top: 3vmin;
    right: 3vmin;
    /* left: 10vh; */
    background-color: black;

    position: fixed;
    transition: 1s;
    /*opacity: 0.9;*/
    color:white;
  }
    
  .Common-hide-menu{
    display: none;
    width: 90vh;
    height: 100vh;
    position: fixed;
    left: -376px;
    transition: 1s;
  }

  .Common-nav-content {
    font-family: 'RIDIBatang';
    font-size: calc(10px + 2vmin);
    text-align: left;
    padding-left: 2vmin;
    padding-top: 1vmin;
    padding-bottom: vmin;
  }

  .Common-empty-20 {
    height: 20px;
  }
