.Main-home-btn {
    position: absolute;
    height : 5rem;
    width : 5rem;
    top: 3rem;
    left: 3rem;
    background-image : url('../img/home.png');  
    background-size : cover;
  }

  .Main-menu-btn {
    position: fixed;
    height : 5rem;
    width : 5rem;
    top: 3rem;
    right: 3rem;
    background-image : url('../img/menu.png');  
    background-size : cover;
  }

  .Main-fixed-top-btn {
    position: fixed;
    height : 6vmin;
    width : 6vmin;
    bottom: 0;
    right: 0;
    margin-right: 20px;
    margin-bottom: 20px;
    opacity: 0.5;

    background-image : url('../img/arrow.png');  
    transform: rotate(90deg);
    background-size : cover;
    background-position : right;
    align-items: right;
    justify-content:right;
  }

  .Main-absolute-top-btn {
    position: absolute;
    height : 6vmin;
    width : 6vmin;
    bottom: 0;
    right: 0;
    margin-right: 20px;
    margin-bottom: 20px;
    opacity: 0.5;
    background-image : url('../img/arrow.png');  
    transform: rotate(90deg);
    background-size : cover;
    background-position : right;
    align-items: right;
    justify-content:right;
  }

  .Main-position-relative {
    position: relative;
  } 

  .Main-comment-area {
    width: 100%;
    min-height: 1000px;
  }


  .Main-background-color {
    background-color: #FFF7F2;

  }

  .Main-text-color {
    color: #261A1A;
  }
  


  @media (min-width: 758px) {
    .Main-home-btn {
      position: absolute;
      margin-left: calc((100vw - 758px)/2) !important;
      height : 6vmin;
      width : 6vmin;
      top: 3vmin;
      left: 3vmin;
      background-image : url('../img/home.png');  
      background-size : cover;
    }
  
    .Main-menu-btn {
      position: fixed;
      /* transform: translateX( -50% ); */
      /* margin-right: calc(100vm - 758px); */
      /* left: calc((100vw - 758px)/2) !important; */
      margin-right: calc((100vw - 758px)/2);
      height : 5vmin;
      width : 5vmin;
      top: 3vmin;
      right: 3vmin;
      background-image : url('../img/menu.png');  
      background-size : cover;
    }
    
    .Main-fixed-top-btn {
      position: fixed;
      height : 5vmin;
      width : 5vmin;
      bottom: 0;
      right: 3vmin;
      margin-right: calc((100vw - 758px)/2);
      margin-bottom: 20px;
      opacity: 0.5;
  
      background-image : url('../img/arrow.png');  
      transform: rotate(90deg);
      background-size : cover;
      background-position : right;
      align-items: right;
      justify-content:right;
    }
  
    .Main-absolute-top-btn {
      position: absolute;
      height : 6vmin;
      width : 6vmin;
      bottom: 0;
      right: 0;
      margin-right: 20px;
      margin-bottom: 20px;
      opacity: 0.5;
      background-image : url('../img/arrow.png');  
      transform: rotate(90deg);
      background-size : cover;
      background-position : right;
      align-items: right;
      justify-content:right;
    }
    
  }